@import url('https://fonts.googleapis.com/css?family=Baloo+Chettan|DM+Sans&display=swap');

html {font-size: 18px;}

body {
  background-color: white;
  font-family: 'DM Sans', sans-serif;
  font-weight: 400;
  line-height: 1.60;
  color: #333;
}

p {margin-bottom: 1.25em;}

h1, h2, h3, h4, h5 {
  margin: 2.75rem 0 1rem;
  font-family: 'Baloo Chettan', sans-serif;
  line-height: 1.15;
}

h1 {
  margin-top: 0;
  font-size: 4.209em;
}

h2 {font-size: 3.157em;}

h3 {font-size: 2.369em;}

h4 {font-size: 1.777em;}

h5 {font-size: 1.333em;}

small, .text_small {font-size: 0.75em;}

.lead {
  font-size: 1.15rem;
}

.ion {
  margin: 0 .5rem
}

.text-bold {
  font-weight: bold
}